export default {
	name:"panel",
	props: {
		list:{}
	},
	data() {
		return {
			switch:false,
			isShow:false
		}
	},
	methods: {
		chanbeSwitch() {
			this.switch = !this.switch;
			this.isShow = !this.isShow;
			/* console.log("点击了");
			console.log(this.switch); */
		},
		tellSuperToCreate() {
			this.$emit("tellSuperToCreate");
		}
	},
	computed:{
		LeftOrRight() {
			if(this.switch==false) {
				return "left";
			}else {
				return "right";
			}
		},
		marginTop() {
			if(this.isShow==false) {
				return 30+'px';
			}else {
				return 12+'px';
			}
		},
		getStatus() {
			if(this.list.status === "在线") {
				return true;
			}else {
				return false;
			}
		}
	}
}
